import React, { useEffect, useRef, useState } from 'react'
import { CustCheckbox, Input, Label, Textarea } from '../../commanComponent'
import { CustomButton, CustomError } from '../../globalComponents'
import { useCustomTranslation } from '../../../translation'
import { createUpdateTravelClaimSchema } from '../../../utils/ValidationSchema'
import { useSelector } from 'react-redux'
import CustomSelect from '../../commanComponent/CustomSelect'
import { validateSchema } from '../../../utils/ValidationHelper'

const AddUpdateTravelClaim = (props) => {
    const { t } = useCustomTranslation()
    const { updateClaimPending, createClaimPending } = useSelector(state => state.travelAndClaim)
    const {
        selectedTravelClaimData,
        openModel,
        handleOpenClose,
        createUpdateClaimRequest,
        travelTypeListData,
        allTravelApprovalData,
        actionBtnType
    } = props
    const [errors, setErrors] = useState({})
    const [billImage, setBillImage] = useState('')
    const [travelClaimFormData, setTravelClaimFormData] = useState({
        travelType: null,
        billNo: '',
        amount: '',
        date: '',
        description: '',
        isDraft: false
    })
    const fileInputRef = useRef(null);

    const handleFromChange = (e, fieldName) => {
        const name = fieldName || e.target?.name;
        const { value, type, checked } = e?.target ? e?.target : { value: e };

        setTravelClaimFormData(prevState => {
            let updatedState = {
                ...prevState,
                [name]: type === 'checkbox' ? checked : value,
            };
            return updatedState;
        });

        // Clear error for the specific field being changed
        setErrors(prevErrors => ({
            ...prevErrors,
            [name]: undefined,
        }));
    };

    const submitCreateUpdateTravelSetting = async (type) => {
        const validationResponse = await validateSchema(createUpdateTravelClaimSchema, travelClaimFormData);
        if (validationResponse.isValid) {
            const {
                billNo,
                amount,
                date,
                description,

                travelType,
                selectTravel
            } = travelClaimFormData

            const formData = new FormData();
            formData.append("travel_id", selectTravel?.value);
            formData.append("travel_type_id", travelType?.value);
            formData.append("bill_no", billNo);
            formData.append("amount", amount);
            formData.append("date", date);
            formData.append("description", description);
            formData.append("bill_image", billImage)
            if (type === "draft") {
                formData.append("is_draft", true);
            }
            if(type==="submit") formData.append("is_draft", false);
            if(selectedTravelClaimData){
                formData.append("_id", selectedTravelClaimData?._id)
            }
            createUpdateClaimRequest(formData)
        } else {
            setErrors(validationResponse.errors);
        }
    }

    useEffect(() => {
        return (() => {
            resetForm()
        })
    }, [openModel])

    useEffect(() => {
        if (selectedTravelClaimData) {
            setTravelClaimFormData({
                selectTravel: { label: selectedTravelClaimData?.travel, value: selectedTravelClaimData?.travel_id },
                travelType: { label: selectedTravelClaimData?.type, value: selectedTravelClaimData?.travel_type_id },
                billNo: selectedTravelClaimData?.bill_no,
                amount: selectedTravelClaimData?.amount,
                description: selectedTravelClaimData?.description,
                date: selectedTravelClaimData?.date,
                isDraft: selectedTravelClaimData?.is_draft,
            })
        }
    }, [selectedTravelClaimData])

    const resetForm = () => {
        setTravelClaimFormData({
            selectTravel: null,
            travelType: null,
            billNo: '',
            amount: '',
            date: '',
            description: '',

        })
        setErrors({})
    }

    const handleFileChange = (e) => {
        const file = e.target.files[0]
        setBillImage(file)
    }


    

    return (
        <>
            <div className={`offcanvas offcanvas-end  ${openModel ? 'show' : ''}`}
                tabIndex={-1}
                style={{
                    visibility: openModel ? 'visible' : 'hidden',
                    width: "800px"
                }}
                id="interviewDetail"
            >
                <div className="offcanvas-header">
                    <h5 className="offcanvas-title  fs-5 fw-bold text-primary">{actionBtnType==="draft"?"Draft":selectedTravelClaimData ? "Update" : "Add"} Travel Claim</h5>
                    <button type="button" className="btn-close" data-bs-dismiss="offcanvas" aria-label="Close"
                        onClick={() => handleOpenClose("travel_rule")}
                    />
                </div>

                <div className="offcanvas-body myRequest ">
                    <div className="formPanel">
                        <div className="row">
                            <div className="col-md-6">
                                <Label text={t("labels.selectTravel")} required={true} />
                                <div className="form-group mb-3">
                                    <CustomSelect
                                        data={allTravelApprovalData}
                                        placeholder={t("placeholders.selectTravel")}
                                        value={travelClaimFormData?.selectTravel || ''}
                                        handleChange={(selectedOption) => handleFromChange(selectedOption, "selectTravel")}
                                        name="selectTravel"
                                    />
                                    {errors?.selectTravel && (
                                        <CustomError text={errors.selectTravel} />
                                    )}
                                </div>
                            </div>
                            <div className="col-md-6">
                                <Label text={t("labels.type")} />
                                <div className="form-group mb-3">
                                    <CustomSelect
                                        data={travelTypeListData}
                                        placeholder={t("placeholders.type")}
                                        value={travelClaimFormData?.travelType || ''}
                                        handleChange={(selectedOption) => handleFromChange(selectedOption, "travelType")}
                                        name="travelType"
                                    />
                                    {errors?.travelType && (
                                        <CustomError text={errors.travelType} />
                                    )}
                                </div>
                            </div>
                            <div className="col-md-6">
                                <Label text={t("labels.billNo")} required={true} />
                                <div className="form-group mb-3">
                                    <Input
                                        type="text"
                                        placeholder={t("placeholders.billNo")}
                                        value={travelClaimFormData?.billNo}
                                        name="billNo"
                                        onChange={(e) => handleFromChange(e)}
                                    />
                                    {errors?.billNo && (
                                        <CustomError text={errors.billNo} />
                                    )}
                                </div>
                            </div>
                            <div className="col-md-6">
                                <Label text={t("labels.amount")} required={true} />
                                <div className="form-group mb-3">
                                    <Input
                                        type="number"
                                        placeholder={t("placeholders.amount")}
                                        value={travelClaimFormData?.amount}
                                        name="amount"
                                        onChange={(e) => handleFromChange(e)}
                                    />
                                    {errors?.amount && (
                                        <CustomError text={errors.amount} />
                                    )}
                                </div>
                            </div>
                            <div className="col-md-6">
                                <Label text={t("labels.date")} required={true} />
                                <div className="form-group mb-3">
                                    <Input
                                        type="date"
                                        placeholder={t("placeholders.date")}
                                        value={travelClaimFormData?.date}
                                        name="date"
                                        onChange={(e) => handleFromChange(e)}
                                    />
                                    {errors?.date && (
                                        <CustomError text={errors.date} />
                                    )}
                                </div>
                            </div>
                            <div className="col-md-12">
                                <Label text={t("labels.description")} />
                                <div className="form-group mb-3">
                                    <Textarea
                                        placeholder={t("placeholders.description")}
                                        onChange={(e) => handleFromChange(e)}
                                        name={"description"}
                                        value={travelClaimFormData?.description}
                                    />
                                    {errors?.description && (
                                        <CustomError text={errors.description} />
                                    )}
                                </div>
                            </div>

                            <div className="col-md-12">
                                <Label text={t("labels.billImage")} />
                                <div className="form-group mb-3">

                                    <input type="file"
                                        id="policyFile"
                                        name="poster"
                                        className={`form-control `}
                                        onChange={handleFileChange}
                                        accept=".png, .jpeg, .doc, .docx, .pdf"
                                        ref={fileInputRef}
                                    />
                                    {errors?.billImage && (
                                        <CustomError text={errors.billImage} />
                                    )}
                                </div>
                            </div>

                            {/* <div className="row mt-2">
                                <div className="col-md-3">
                                    <div className="form-group mb-3">
                                        <Label text={t("labels.isDraft")} />
                                    </div>
                                </div>
                                <div className="col-md-9">
                                    <div className="form-group mb-3">
                                        <CustCheckbox
                                            key={"isDraft"}
                                            id={`isDraft`}
                                            checked={travelClaimFormData?.isDraft}
                                            handleChange={(e) => handleFromChange(e)}
                                            name={"isDraft"}
                                        />
                                    </div>
                                </div>
                            </div> */}
                        </div>


                    </div>
                </div>
                <div className="offcanvas-footer">
                    <div className="d-flex align-items-center justify-content-end gap-3">
                        {!selectedTravelClaimData && <CustomButton
                            text={"Draft"}
                            classNames={"btn btn-org-gradient"}
                            handleClick={() => submitCreateUpdateTravelSetting("draft")}
                            isDisabled={createClaimPending}
                        />}
                        <CustomButton
                            text={actionBtnType ?"Submit":selectedTravelClaimData ? t("btns.update") : t("btns.submit")}
                            classNames={"btn btn-primary"}
                            handleClick={() => submitCreateUpdateTravelSetting("submit")}
                            isDisabled={selectedTravelClaimData  ? updateClaimPending : createClaimPending}
                        />
                    </div>
                </div>

            </div>
            {openModel && <div className="modal-backdrop fade show"></div>}
        </>
    )
}

export default AddUpdateTravelClaim
