import React, { useEffect, useMemo, useRef, useState } from 'react'

import { useCustomTranslation } from '../../../translation'
import { CustomTable } from '../../reuseableComponents'
import { dateFormate } from '../../../utils/common'

const tempData =[
    {travel_type:"car", amount:'111',date:'2024-03-22',bill_no:'1234'}
]
const ViewTravelClaimDetail = (props) => {
    const { t } = useCustomTranslation()
    const {
        selectedTravelClaimData,
        openModel,
        handleOpenClose,
        toggleBillDetail
    } = props

    const fields=[
        {key:'sn', label: 'S.N' },
        {key:'travel_type_name', label: 'Travel Type' },
        {key:'amount', label: 'Amount' },
        {key:'date', label: 'Date' },
        {key:'bill_no', label: 'Bill No' },
        {key:'bill_image', label: 'Bill ' },
    ]

        const scopedSlots = useMemo(() => ({
            sn: (_, index) => (
                <span>{index + 1}</span>
            ),
           
            bill_image: item => (
                <span 
                onClick={() => toggleBillDetail(item)}
                >
                <img src="images/view-icon.svg" alt width="32" />
              </span>
            ),
            date: item => (
                <span >
                 {dateFormate(item?.created_at)}
              </span>
            ),
         
        }), [selectedTravelClaimData]);

    return (
        <>
            <div className={`offcanvas offcanvas-end  ${openModel ? 'show' : ''}`}
                tabIndex={-1}
                style={{
                    visibility: openModel ? 'visible' : 'hidden',
                    width: "800px"
                }}
                id="interviewDetail"
            >
                <div className="offcanvas-header">
                    <h5 className="offcanvas-title  fs-5 fw-bold text-primary">Travel Detail</h5>
                    <button type="button" className="btn-close" data-bs-dismiss="offcanvas" aria-label="Close"
                        onClick={() => handleOpenClose()}
                    />
                </div>


                <div className="offcanvas-body myRequest ">
                    <div className="formPanel">


                        <CustomTable
                            data={selectedTravelClaimData?.travel_details ||tempData}
                            fields={fields}
                            // isLoading={getClaimPending}
                            pagination={true}
                            // currentPage={currentPage}
                            // setCurrentPage={setCurrentPage}
                            // totalPages={claimRequestData?.totalPages}
                            isHeader={false}
                            tableStyle={{ padding: "0px", boxShadow: "none" }}
                            scopedSlots={scopedSlots}
                        />

                    </div>
                </div>
                <div className="offcanvas-footer">

                </div>

            </div>
            {openModel && <div className="modal-backdrop fade show"></div>}
        </>
    )
}

export default ViewTravelClaimDetail
