import React from 'react'
import { dateFormate, toTitleCase } from '../../utils/common';
import moment from 'moment';

const ProgressModel = (props) => {
    const { openModel, handleOpenClose,progressData,processType } = props
    const renderHierarchy = () => {
        return progressData[0]?.approver_data?.map((data, ind) => {
            const isApproved = data.action_type === "approve";
            const isRejected = data.action_type === "reject";

            return (
                <li
                    className={`select ${isApproved ? "active" : isRejected ? "nonactive" : ""}`} 
                    key={ind}
                >
                    <figure className="m-0">
                        <img
                            src={isApproved ? "images/greenRight.svg" : isRejected ? "images/redCross.svg" : "images/greenRight.svg"} 
                            alt=""
                            width="45"
                        />
                    </figure>
                    <strong>
                        {/* <strong>
                            {`${data?.name} ( ${data?.role_name} )`}<b>{moment(data?.date).format("DD-MM-YYYY LT")}</b> <b>{data?.comment}</b> 
                        </strong> */}
                        {isApproved ? "Approved by " : isRejected ? "Rejected by " : "Pending by "} 
                        {`${data?.name} ( ${data?.role_name} )`}<b>{moment(data?.date).format("DD-MM-YYYY LT")}</b> <b>{data?.comment}</b> 
                        {/* {data?.role_name} */}
                        {/* <b>{data?.approver_name}</b> */}
                    </strong>
                </li>
            );
        });
    };
  return (
    <>
    <div
            className={`offcanvas offcanvas-end applyLeaveCanvas ${openModel ? 'show' : ''}`}
            tabIndex={-1}
            id="attendanceCorrection"
            style={{
                visibility: openModel ? 'visible' : 'hidden',
                width: "800px"
            }}
        >
            <div className="offcanvas-header">
                <h5 className="offcanvas-title  fs-5 fw-bold text-primary text-capitalize">
                {toTitleCase(processType)} Process
                </h5>
                <button
                    type="button"
                    className="btn-close"
                    data-bs-dismiss="offcanvas"
                    aria-label="Close"
                    onClick={() => { handleOpenClose() }}
                />
            </div>
            <div className="offcanvas-body">
                <div className="detailsCandidate">
                    <ul className="detailsList">
                        <li className="select active">
                            <figure className="m-0">
                                <img src="images/greenRight.svg" alt="" width={45} />
                            </figure>
                            <strong className='text-capitalize'>
                            {toTitleCase(processType)} Applied<b>{progressData &&progressData?.length>0 &&progressData[0]?.date }</b>
                            </strong>
                        </li>
                        {progressData &&progressData?.length >0 && renderHierarchy() }
                    </ul>
                </div>
            </div>
            <div className="offcanvas-footer">
                <div className="d-flex align-items-center justify-content-end gap-3">
                    
                </div>
            </div>
            
        </div>
        {openModel && <div className="modal-backdrop fade show"></div>}
        </>
  )
}

export default ProgressModel
