import React, { useEffect, useMemo, useState } from 'react'
import { CustomButton } from '../../globalComponents'
import { CustomTable, CustomTabs } from '../../reuseableComponents'
import { Icon } from '../../commanComponent'
import { useDispatch, useSelector } from 'react-redux'
import { leaveAction, travelAndClaimAction } from '../../../core/resource/actions'
import { pagination } from '../../../utils/Constants.config'
import AddUpdateTravelClaim from './AddUpdateTravelClaim'
import ApproveRejectModal from '../../modal/ApproveRejectModal'
import { useCustomTranslation } from '../../../translation'
import { dateFormate, getBadgeColor } from '../../../utils/common'
import Image from '../../commanComponent/Image'
import ProgressModel from '../../leave_management/ProgressModel'
import ViewBillDetail from './ViewBillDetail'
import { useNavigate } from 'react-router-dom'
import ViewTravelClaimDetail from './ViewTravelClaimDetail'

const TravelClaim = () => {
    const { t } = useCustomTranslation()
    const dispatch = useDispatch()
    const navigate = useNavigate()
    const [claimRequestData, setClaimRequestData] = useState('')
    const {
        getLeaveProgressPending,
        getLeaveProgressRes,
        getLeaveProgressError,
    } = useSelector(state => state.leave);
    const {
        createClaimPending,
        createClaimRes,
        createClaimError,

        updateClaimPending,
        updateClaimRes,
        updateClaimError,

        deleteClaimPending,
        deleteClaimRes,
        deleteClaimError,

        getClaimPending,
        getClaimRes,
        getClaimError,

        getAllTravelTypePending,
        getAllTravelTypeRes,
        getAllTravelTypeError,

        getAllTravelApprovalDataPending,
        getAllTravelApprovalDataRes,
        getAllTravelApprovalDataError,
    } = useSelector(state => state.travelAndClaim)
    const [currentPage, setCurrentPage] = useState(1)
    const [isOpenTravelClaim, setIsOpenTravelClaim] = useState(false)

    const [travelTypeListData, setTravelTypeListData] = useState('')
    const [selectedTravelClaimData, setSelectedTravelClaimData] = useState('')
    const [deleteTravelClaimId, setDeleteTravelClaimId] = useState('')
    const [isShowDeleteTravelClaimModal, setIsShowDeleteTravelClaimModal] = useState(false)
    const [actionBtnType, setActionBtnType] = useState('')
    const [progressData, setProgressData] = useState('')
    const [openProgressModel, setOpenProgressModel] = useState(false)
    const [isOpenBillDetailModal, setIsOpenBillDetailModal] = useState(false)
    const [isOpenTravelDetailModal, setIsOpenTravelDetailModal] = useState(false)
    const [claimType, setClaimType] = useState('submit')
    const [billImageData, setBillImageData] = useState('')

    const fields = [
        { key: 'sn', label: 'S.N' },
        { key: 'travel', label: 'Travel' },
        { key: 'emp_id', label: 'Employee Code' },
        { key: 'name', label: 'Name' },
        { key: 'approvers_status', label: 'Status' },
        { key: 'created_at', label: 'Created Date' },
        { key: 'travel_details', label: 'Claim Detail' },
        { key: 'action', label: 'Action' }
    ]
    const toggleBillDetail = (data) => {
        if (data) {
            setBillImageData(data)
        } else {
            setBillImageData('')
        }
        setIsOpenBillDetailModal(!isOpenBillDetailModal)

    }

    const toggleTravelDetail = (data) => {
        if (data) {
            setSelectedTravelClaimData(data)
        } else {
            setSelectedTravelClaimData('')
        }
        setIsOpenTravelDetailModal(!isOpenTravelDetailModal)

    }
    const scopedSlots = useMemo(() => ({
        sn: (_, index) => (
            <span>{index + 1}</span>
        ),
        approvers_status: item => (
            <span
                className={`badge ${getBadgeColor(item?.is_draft ? 'inactive' : item.approvers_status)} text-capitalize`}

            >
                {item?.is_draft ? "Draft" : item.approvers_status}
            </span>
        ),
        travel_details: item => (
            <span
                onClick={() => toggleTravelDetail(item)}
            >
                <img src="images/info-orange-bg.svg" alt width="32" />
            </span>
        ),
        created_at: item => (
            <span >
                {dateFormate(item?.created_at)}
            </span>
        ),
        action: item => (
            <>

                {<div className="btn-group actionDrp">
                    <button type="button" className="btn dropdown-toggle-split"
                        data-bs-toggle="dropdown" aria-expanded="false">
                        <i className="bi bi-three-dots-vertical"></i>
                    </button>
                    <ul className="dropdown-menu">
                        <li>
                            <span className="dropdown-item"
                                onClick={() => {
                                    onHandleProgressModel("claim", item?._id)
                                }}
                            >
                                <Icon className={"fa fa-check me-1"} />Approval Status</span>
                        </li>
                        {item?.is_draft && <>
                            <li>
                                <span className="dropdown-item"
                                    onClick={() => {
                                        navigateToAddUpdate(item, 'draft');
                                    }}
                                >
                                    <Icon className={"fa fa-file me-1"} />Draft</span>
                            </li>
                            <li>
                                <span className="dropdown-item"
                                    onClick={() => {
                                        navigateToAddUpdate(item);
                                    }}
                                >
                                    <Icon className={"fa fa-edit me-1"} />Update</span>
                            </li>
                            <li>
                                <span className="dropdown-item"
                                    onClick={() => handleTravelClaimDeleteModal(item._id)}
                                >
                                    <Icon className={"bi bi-trash me-1"} />Delete</span>
                            </li>
                        </>}
                    </ul>
                </div>}
            </>
        )
    }), []);

    /* approval process b*/
    const onHandleProgressModel = (type, id) => {
        if (type || id) {
            const payload = {
                id: id || '',
                type: type || ''
            }
            dispatch(leaveAction.getLeaveProgressRequest(payload))
        }

        setOpenProgressModel(!openProgressModel)

    }
    useEffect(() => {
        if (!getLeaveProgressPending) {
            if (getLeaveProgressError) {
                return;
            }
            if (getLeaveProgressRes) {
                if (getLeaveProgressRes?.code === 200) {
                    setProgressData(getLeaveProgressRes?.data)
                }
            }
        }
    }, [
        getLeaveProgressRes,
        getLeaveProgressError,
        getLeaveProgressPending
    ]);
    const toggleAddUpdateTravelClaim = (item, type) => {
        if (item) {
            setSelectedTravelClaimData(item)
            setActionBtnType(type)
        } else {
            setSelectedTravelClaimData('')
        }
        setIsOpenTravelClaim(!isOpenTravelClaim)
    }

    useEffect(() => {
        getTravelRequestData()
    }, [currentPage, claimType])
    const getTravelRequestData = () => {
        const payload = {
            is_draft: claimType === "draft" ? true : false,
            page: currentPage,
            limit: pagination.limit
        }
        dispatch(travelAndClaimAction.getClaimRequest(payload));
    }

    useEffect(() => {
        if (!getClaimPending) {
            if (getClaimError) {
                return;
            }
            if (getClaimRes) {
                if (getClaimRes?.code === 200) {
                    setClaimRequestData(getClaimRes?.data)

                }
            }
        }
    }, [
        getClaimPending,
        getClaimRes,
        getClaimError,
    ]);



    /*Create Claim Data */
    const createUpdateClaimRequest = (payload) => {
        if (selectedTravelClaimData) {
            payload["_id"] = selectedTravelClaimData?._id;
            dispatch(travelAndClaimAction.updateClaimRequest(payload));
        } else {
            dispatch(travelAndClaimAction.createClaimRequest(payload));
        }
    }
    useEffect(() => {
        if (!createClaimPending) {
            if (createClaimError) {
                dispatch(travelAndClaimAction.handleClearTravelStore("HANDLE_CLEAR_CREATE_CLAIM_STORE"))
                return;
            }
            if (createClaimRes) {
                if (createClaimRes?.code === 200) {
                    dispatch(travelAndClaimAction.handleClearTravelStore("HANDLE_CLEAR_CREATE_CLAIM_STORE"))
                    toggleAddUpdateTravelClaim()
                    getTravelRequestData()

                }
            }
        }
    }, [
        createClaimError,
        createClaimRes,
        createClaimPending
    ]);
    useEffect(() => {
        if (!updateClaimPending) {
            if (updateClaimError) {
                dispatch(travelAndClaimAction.handleClearTravelStore("HANDLE_CLEAR_UPDATE_CLAIM_STORE"))
                return;
            }
            if (updateClaimRes) {
                if (updateClaimRes?.code === 200) {
                    dispatch(travelAndClaimAction.handleClearTravelStore("HANDLE_CLEAR_UPDATE_CLAIM_STORE"))
                    toggleAddUpdateTravelClaim()
                    getTravelRequestData()

                }
            }
        }
    }, [
        updateClaimError,
        updateClaimRes,
        updateClaimPending
    ]);

    /*  Delete Travel Claim   */
    const handleTravelClaimDeleteModal = (id) => {
        if (id) {
            setDeleteTravelClaimId(id)
        }
        setIsShowDeleteTravelClaimModal(!isShowDeleteTravelClaimModal)
    }

    const handleDeleteTravelClaim = (type) => {
        if (type) {
            const params = {
                _id: deleteTravelClaimId
            }
            dispatch(travelAndClaimAction?.deleteClaimRequest(params))
        } else {
            handleTravelClaimDeleteModal()
        }
    }
    useEffect(() => {
        if (!deleteClaimPending) {
            if (deleteClaimError) {
                dispatch(travelAndClaimAction.handleClearTravelStore("HANDLE_CLEAR_DELETE_CLAIM_STORE"))
                return;
            }
            if (deleteClaimRes) {
                if (deleteClaimRes?.code === 200) {
                    dispatch(travelAndClaimAction.handleClearTravelStore("HANDLE_CLEAR_DELETE_CLAIM_STORE"))
                    getTravelRequestData()
                    setIsShowDeleteTravelClaimModal(!isShowDeleteTravelClaimModal)

                }
            }
        }
    }, [
        deleteClaimError,
        deleteClaimRes,
        deleteClaimPending
    ]);




    // const travelClaim = [
    //     { name: "Submitted", onClickFun: (id) => getTravelClaim(id), id: "submit_claim" },
    //     { name: "Draft", onClickFun: (id) => getTravelClaim(id), id: "draft_claim" },
    // ]

    // const getTravelClaim = () => {

    // }


    const navigateToAddUpdate = (data,type) => {
        if (data) {
            dispatch(travelAndClaimAction.selectedTravelClaimDetail({...data,action_type:type}))
        }
        navigate("add-update-travel-claims")
    }

    return (
        <div className='cardBox myRequest mb-3 employeeHistory'>
            <div className="d-flex align-items-center justify-content-between gap-3">
                <div className="cardHead d-flex">
                    <img src="images/myrequest-icon.svg" alt="" width={35} />
                    <h4 className="ms-2 ">Travel Claim</h4>
                </div>
                <CustomButton
                    text={"Add Travel Claim"}
                    classNames={"btn btn-primary"}
                    icon={<Icon className="fa fa-plus" />}
                    handleClick={() => navigateToAddUpdate()}
                />
            </div>
            <hr />
            <div className="requestdata">
                <div className='row'>
                    <div className='col-6'>
                        <div className="d-flex align-items-center mb-4 ">
                            <span className="fw-bold fs-6 text-black">Claim Status</span>
                            <ul className="nav nav-tabs leaveTabs ms-3">
                                <li className="nav-item">
                                    <CustomButton
                                        text="Submitted"
                                        classNames={`nav-link px-4 ${claimType === "submit" ? "active" : ""}`}
                                        handleClick={() => setClaimType("submit")}
                                    />
                                </li>
                                <li className="nav-item">
                                    <CustomButton
                                        text="Draft"
                                        classNames={`nav-link px-4 ${claimType == "draft" ? "active" : ""}`}
                                        handleClick={() => setClaimType("draft")}
                                    />
                                </li>
                            </ul>
                        </div>
                    </div>
                </div>
            </div>

            {/* <CustomTabs tabs={travelClaim} type="travelClaim" />

            <div className="tab-content">
                <div className="tab-pane fade active show" id="submit_claim"> */}
            <CustomTable
                data={claimRequestData?.data}
                fields={fields}
                isLoading={getClaimPending}
                pagination={true}
                currentPage={currentPage}
                setCurrentPage={setCurrentPage}
                totalPages={claimRequestData?.totalPages}
                isHeader={false}
                tableStyle={{ padding: "0px", boxShadow: "none" }}
                scopedSlots={scopedSlots}
            />
            {/* </div>
                <div className="tab-pane fade show" id="draft_claim">
                    <CustomTable
                        data={claimRequestData?.data}
                        fields={fields}
                        isLoading={getClaimPending}
                        pagination={true}
                        currentPage={currentPage}
                        setCurrentPage={setCurrentPage}
                        totalPages={claimRequestData?.totalPages}
                        isHeader={false}
                        tableStyle={{ padding: "0px", boxShadow: "none" }}
                        scopedSlots={scopedSlots}
                    />
                </div>
            </div> */}

            {isOpenTravelClaim && <AddUpdateTravelClaim
                handleOpenClose={toggleAddUpdateTravelClaim}
                openModel={isOpenTravelClaim}
                createUpdateClaimRequest={createUpdateClaimRequest}
                selectedTravelClaimData={selectedTravelClaimData}
                actionBtnType={actionBtnType}
            />}

            {isShowDeleteTravelClaimModal && (
                <ApproveRejectModal
                    onApproveReject={handleDeleteTravelClaim}
                    isShow={isShowDeleteTravelClaimModal}
                    desc={t("alerts.deletePopup.desc").replace('$1', "Travel Claim")}
                    btnRightLoading={deleteClaimPending}
                    btnTextRight={t("btns.delete")}
                    btnTextLeft={t("btns.cancel")}

                />)
            }
            {openProgressModel && <ProgressModel
                openModel={openProgressModel}
                handleOpenClose={onHandleProgressModel}
                progressData={progressData}
                processType={"Travel & Claim"}
            />}
            {isOpenBillDetailModal && <ViewBillDetail
                handleOpenClose={toggleBillDetail}
                openModel={isOpenBillDetailModal}
                billImageData={billImageData}
            />}
            {isOpenTravelDetailModal && <ViewTravelClaimDetail
                handleOpenClose={toggleTravelDetail}
                openModel={isOpenTravelDetailModal}
                selectedTravelClaimData={selectedTravelClaimData}
                toggleBillDetail={toggleBillDetail}
            />}
        </div>
    )
}
export default TravelClaim
