import React, { useEffect, useRef, useState } from 'react'

import { useCustomTranslation } from '../../../translation'
import { CustomError } from '../../commanComponent'


const ViewBillDetail = (props) => {
    const { t } = useCustomTranslation()
    const {
        billImageData,
        openModel,
        handleOpenClose,
    } = props


    return (
        <>
            {/* Modal */}
            <div className={`modal fade attendanceLog ${openModel ? 'show d-block' : ''}`}
                tabIndex="-1"
                style={{ zIndex: 1060 }} 
                aria-hidden={!openModel}>

                <div className="modal-dialog modal-dialog-centered" style={{ maxWidth: '700px' }}>
                    <div className="modal-content rounded-0 border-0">
                        <div className="modal-header ">
                            <h1 className="modal-title fs-5 fw-bold text-primary">Bill</h1>
                            <button type="button" className="btn-close" data-bs-dismiss="modal" aria-label="Close"
                                onClick={handleOpenClose}
                            />
                        </div>
                        <div className="modal-body pt-3">
                            <div className="formPanel">
                            {billImageData?.bill_image ?<div className='d-flex gap-3'>
                                <img src={billImageData?.bill_image} alt="Bill Image" height={210} width={210} />
                                
                                </div>:
                               <span className='d-flex justify-content-center'> <CustomError  text={"No Image Available"}/></span>
                                }
                                
                            </div>
                        </div>
                    </div>
                </div>
            </div>

            {/* Backdrop - Higher z-index than canvas modal */}
            {openModel && (
                <div className="modal-backdrop fade show" style={{ zIndex: 1055 }}></div>
            )}

        </>
    )
}

export default ViewBillDetail
